"use client";

import { useState, useEffect, useMemo } from "react";
import { useRouter } from "next/navigation";
import {
    ArrowRight,
    ExternalLink,
    Search,
    X,
    SlidersHorizontal,
    Filter,
    Grid3X3,
    Layers,
    Package,
    TrendingUp,
    ChevronDown,
    ChevronUp,
    FileText,
    Users,
    Building,
    Briefcase,
    Map,
    Calendar,
    FileCheck,
    Clipboard,
    DollarSign,
    Clock,
    Receipt,
    User as UserIcon,
    Book,
    List,
} from "lucide-react";

interface MenuItem {
    id: string;
    text: string;
    icon: any;
    path: string;
    description: string;
    groupName: string;
    permissions?: {
        phan_quyen?: string[];
    };
}

const Dashboard = () => {
    const router = useRouter();
    const [searchQuery, setSearchQuery] = useState("");
    const [selectedGroup, setSelectedGroup] = useState("all");
    const [displayMode, setDisplayMode] = useState(() =>
        typeof window !== "undefined"
            ? localStorage.getItem("menuDisplayMode") || "grouped"
            : "grouped",
    );
    const [showFilters, setShowFilters] = useState(false);
    const [isNavigating, setIsNavigating] = useState(false);
    const [collapsedGroups, setCollapsedGroups] = useState<Set<string>>(
        new Set(),
    );

    // Mock user data - trong thực tế sẽ lấy từ context hoặc API
    const userData = useMemo(
        () => ({
            phan_quyen: "Admin",
        }),
        [],
    );

    // Mock menu items - sẽ được thay thế bằng API call
    const menuItems: MenuItem[] = useMemo(
        () => [
            // Trang chủ
            {
                id: "trang_chu",
                text: "Trang chủ",
                icon: Grid3X3,
                path: "/dashboard",
                description: "Tổng quan hệ thống",
                groupName: "Trang chủ",
            },

            // Quản lý người dùng
            {
                id: "quan_ly_nguoi_dung",
                text: "Quản lý người dùng",
                icon: Users,
                path: "/nguoi-dung",
                description: "Quản lý người dùng hệ thống",
                groupName: "Quản lý hệ thống",
            },

            // Danh mục
            {
                id: "danh_muc_phong_ban",
                text: "Phòng ban",
                icon: Building,
                path: "/phong-ban",
                description: "Quản lý phòng ban",
                groupName: "Danh mục",
            },
            {
                id: "danh_muc_chuc_vu",
                text: "Chức vụ",
                icon: Briefcase,
                path: "/chuc-vu",
                description: "Quản lý chức vụ",
                groupName: "Danh mục",
            },

            // Khách hàng
            {
                id: "khach_hang",
                text: "Khách hàng",
                icon: Users,
                path: "/khach-hang",
                description: "Quản lý thông tin khách hàng",
                groupName: "Khách hàng",
            },
            {
                id: "customer_map",
                text: "Bản đồ khách hàng",
                icon: Map,
                path: "/customer-map",
                description: "Xem khách hàng trên bản đồ",
                groupName: "Khách hàng",
            },
            {
                id: "ke_hoach_cskh",
                text: "Kế hoạch CSKH",
                icon: Calendar,
                path: "/ke-hoach-cskh",
                description: "Quản lý kế hoạch chăm sóc khách hàng",
                groupName: "Khách hàng",
            },

            // Sản phẩm
            {
                id: "phan_loai_hang_hoa",
                text: "Phân loại hàng hóa",
                icon: Layers,
                path: "/phan-loai-hang-hoa",
                description: "Phân loại hàng hóa dịch vụ",
                groupName: "Quản lý sản phẩm",
            },
            {
                id: "danh_muc_hang_hoa",
                text: "Danh mục hàng hóa",
                icon: Package,
                path: "/danh-muc-hang-hoa",
                description: "Quản lý danh mục hàng hóa",
                groupName: "Quản lý sản phẩm",
            },

            // Báo giá & Hợp đồng
            {
                id: "bao_gia",
                text: "Báo giá",
                icon: FileText,
                path: "/bao-gia",
                description: "Quản lý báo giá",
                groupName: "Báo giá & Hợp đồng",
            },
            {
                id: "hop_dong",
                text: "Hợp đồng",
                icon: FileCheck,
                path: "/hop-dong",
                description: "Quản lý hợp đồng",
                groupName: "Báo giá & Hợp đồng",
            },
            {
                id: "phu_luc_hop_dong",
                text: "Phụ lục hợp đồng",
                icon: Clipboard,
                path: "/phu-luc-hop-dong",
                description: "Quản lý phụ lục hợp đồng",
                groupName: "Báo giá & Hợp đồng",
            },

            // Thanh toán
            {
                id: "thanh_toan",
                text: "Thanh toán",
                icon: DollarSign,
                path: "/thanh-toan",
                description: "Quản lý thanh toán",
                groupName: "Thanh toán",
            },
            {
                id: "lich_su_thanh_toan",
                text: "Lịch sử thanh toán",
                icon: Clock,
                path: "/lich-su-thanh-toan",
                description: "Xem lịch sử thanh toán",
                groupName: "Thanh toán",
            },
            {
                id: "hoa_don_vat",
                text: "Hóa đơn VAT",
                icon: Receipt,
                path: "/hoa-don-vat",
                description: "Quản lý hóa đơn VAT",
                groupName: "Thanh toán",
            },

            // Hệ thống
            {
                id: "profile",
                text: "Hồ sơ cá nhân",
                icon: UserIcon,
                path: "/profile",
                description: "Quản lý thông tin cá nhân",
                groupName: "Hệ thống",
            },
            {
                id: "huong_dan",
                text: "Hướng dẫn sử dụng",
                icon: Book,
                path: "/huong-dan-su-dung",
                description: "Hướng dẫn sử dụng hệ thống",
                groupName: "Hệ thống",
            },
        ],
        [],
    );

    // Lấy danh sách các nhóm
    const groups = useMemo(() => {
        const uniqueGroups = Array.from(
            new Set(menuItems.map((item) => item.groupName)),
        );
        return uniqueGroups.map((groupName) => ({
            name: groupName,
            items: menuItems.filter((item) => item.groupName === groupName),
        }));
    }, [menuItems]);

    // Check permission - memoized to prevent infinite loop
    const checkPermission = useMemo(() => {
        return (item: MenuItem) => {
            if (!item.permissions || !item.permissions.phan_quyen) {
                return true;
            }
            return (
                item.permissions.phan_quyen.includes(userData.phan_quyen) ||
                item.permissions.phan_quyen.includes("All")
            );
        };
    }, [userData.phan_quyen]);

    // Filter items using useMemo instead of useEffect
    const filteredItems = useMemo(() => {
        let filtered = menuItems.filter(checkPermission);

        if (selectedGroup !== "all") {
            filtered = filtered.filter(
                (item) => item.groupName === selectedGroup,
            );
        }

        if (searchQuery) {
            filtered = filtered.filter(
                (item) =>
                    item.text
                        .toLowerCase()
                        .includes(searchQuery.toLowerCase()) ||
                    item.description
                        .toLowerCase()
                        .includes(searchQuery.toLowerCase()),
            );
        }

        return filtered;
    }, [searchQuery, selectedGroup, menuItems, checkPermission]);

    // Toggle group collapse
    const toggleGroupCollapse = (groupName: string) => {
        setCollapsedGroups((prev) => {
            const newSet = new Set(prev);
            if (newSet.has(groupName)) {
                newSet.delete(groupName);
            } else {
                newSet.add(groupName);
            }
            return newSet;
        });
    };

    // Handle navigation
    const handleNavigation = (path: string) => {
        setIsNavigating(true);
        router.push(path);
    };

    // Toggle display mode
    const toggleDisplayMode = () => {
        const newMode = displayMode === "grouped" ? "list" : "grouped";
        setDisplayMode(newMode);
        if (typeof window !== "undefined") {
            localStorage.setItem("menuDisplayMode", newMode);
        }
    };

    return (
        <div className="min-h-screen bg-gray-50 p-6">
            {/* Header */}
            <div className="bg-white rounded-lg shadow-sm p-6 mb-6">
                <div className="flex flex-col lg:flex-row lg:items-center lg:justify-between">
                    <div>
                        <h1 className="text-3xl font-bold text-gray-900 mb-2">
                            TRG CRM Dashboard
                        </h1>
                        <p className="text-gray-600">
                            Chào mừng trở lại! Chọn chức năng để bắt đầu làm
                            việc.
                        </p>
                    </div>

                    {/* Search and Filters */}
                    <div className="mt-4 lg:mt-0 flex flex-col sm:flex-row gap-3">
                        <div className="relative">
                            <Search className="absolute left-3 top-1/2 transform -translate-y-1/2 text-gray-400 w-5 h-5" />
                            <input
                                type="text"
                                placeholder="Tìm kiếm chức năng..."
                                value={searchQuery}
                                onChange={(e) => setSearchQuery(e.target.value)}
                                className="pl-10 pr-4 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-500 focus:border-transparent outline-none w-64"
                            />
                        </div>

                        <button
                            onClick={() => setShowFilters(!showFilters)}
                            className="flex items-center px-4 py-2 border border-gray-300 rounded-lg hover:bg-gray-50 transition"
                        >
                            <SlidersHorizontal className="w-5 h-5 mr-2" />
                            Bộ lọc
                        </button>

                        <button
                            onClick={toggleDisplayMode}
                            className="flex items-center px-4 py-2 border border-gray-300 rounded-lg hover:bg-gray-50 transition"
                        >
                            {displayMode === "grouped" ? (
                                <List className="w-5 h-5 mr-2" />
                            ) : (
                                <Grid3X3 className="w-5 h-5 mr-2" />
                            )}
                            {displayMode === "grouped" ? "Nhóm" : "Danh sách"}
                        </button>
                    </div>
                </div>

                {/* Filters */}
                {showFilters && (
                    <div className="mt-4 pt-4 border-t border-gray-200">
                        <div className="flex flex-wrap gap-2">
                            <button
                                onClick={() => setSelectedGroup("all")}
                                className={`px-4 py-2 rounded-lg transition ${
                                    selectedGroup === "all"
                                        ? "bg-blue-600 text-white"
                                        : "bg-gray-100 text-gray-700 hover:bg-gray-200"
                                }`}
                            >
                                Tất cả
                            </button>
                            {groups.map((group) => (
                                <button
                                    key={group.name}
                                    onClick={() => setSelectedGroup(group.name)}
                                    className={`px-4 py-2 rounded-lg transition ${
                                        selectedGroup === group.name
                                            ? "bg-blue-600 text-white"
                                            : "bg-gray-100 text-gray-700 hover:bg-gray-200"
                                    }`}
                                >
                                    {group.name}
                                </button>
                            ))}
                        </div>
                    </div>
                )}
            </div>

            {/* Content */}
            <div className="grid gap-6">
                {displayMode === "grouped" ? (
                    // Grouped view
                    groups.map((group) => {
                        const groupItems = filteredItems.filter(
                            (item) => item.groupName === group.name,
                        );
                        if (groupItems.length === 0) return null;

                        const isCollapsed = collapsedGroups.has(group.name);

                        return (
                            <div
                                key={group.name}
                                className="bg-white rounded-lg shadow-sm overflow-hidden"
                            >
                                <button
                                    onClick={() =>
                                        toggleGroupCollapse(group.name)
                                    }
                                    className="w-full px-6 py-4 bg-gradient-to-r from-blue-50 to-indigo-50 border-b border-gray-200 flex items-center justify-between hover:from-blue-100 hover:to-indigo-100 transition"
                                >
                                    <h2 className="text-lg font-semibold text-gray-900">
                                        {group.name}
                                    </h2>
                                    {isCollapsed ? (
                                        <ChevronDown className="w-5 h-5 text-gray-600" />
                                    ) : (
                                        <ChevronUp className="w-5 h-5 text-gray-600" />
                                    )}
                                </button>

                                {!isCollapsed && (
                                    <div className="p-6">
                                        <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 xl:grid-cols-4 gap-4">
                                            {groupItems.map((item) => {
                                                const Icon = item.icon;
                                                return (
                                                    <button
                                                        key={item.id}
                                                        onClick={() =>
                                                            handleNavigation(
                                                                item.path,
                                                            )
                                                        }
                                                        className="p-4 border border-gray-200 rounded-lg hover:border-blue-300 hover:shadow-md transition-all duration-200 text-left group"
                                                    >
                                                        <div className="flex items-start space-x-3">
                                                            <div className="p-2 bg-blue-50 rounded-lg group-hover:bg-blue-100 transition">
                                                                <Icon className="w-6 h-6 text-blue-600" />
                                                            </div>
                                                            <div className="flex-1 min-w-0">
                                                                <h3 className="font-medium text-gray-900 group-hover:text-blue-600 transition">
                                                                    {item.text}
                                                                </h3>
                                                                <p className="text-sm text-gray-600 mt-1 line-clamp-2">
                                                                    {
                                                                        item.description
                                                                    }
                                                                </p>
                                                            </div>
                                                            <ArrowRight className="w-4 h-4 text-gray-400 group-hover:text-blue-600 transition" />
                                                        </div>
                                                    </button>
                                                );
                                            })}
                                        </div>
                                    </div>
                                )}
                            </div>
                        );
                    })
                ) : (
                    // List view
                    <div className="bg-white rounded-lg shadow-sm overflow-hidden">
                        <div className="p-6">
                            <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 xl:grid-cols-4 gap-4">
                                {filteredItems.map((item) => {
                                    const Icon = item.icon;
                                    return (
                                        <button
                                            key={item.id}
                                            onClick={() =>
                                                handleNavigation(item.path)
                                            }
                                            className="p-4 border border-gray-200 rounded-lg hover:border-blue-300 hover:shadow-md transition-all duration-200 text-left group"
                                        >
                                            <div className="flex items-start space-x-3">
                                                <div className="p-2 bg-blue-50 rounded-lg group-hover:bg-blue-100 transition">
                                                    <Icon className="w-6 h-6 text-blue-600" />
                                                </div>
                                                <div className="flex-1 min-w-0">
                                                    <h3 className="font-medium text-gray-900 group-hover:text-blue-600 transition">
                                                        {item.text}
                                                    </h3>
                                                    <p className="text-sm text-gray-600 mt-1 line-clamp-2">
                                                        {item.description}
                                                    </p>
                                                    <p className="text-xs text-gray-500 mt-2">
                                                        {item.groupName}
                                                    </p>
                                                </div>
                                                <ArrowRight className="w-4 h-4 text-gray-400 group-hover:text-blue-600 transition" />
                                            </div>
                                        </button>
                                    );
                                })}
                            </div>
                        </div>
                    </div>
                )}

                {filteredItems.length === 0 && (
                    <div className="bg-white rounded-lg shadow-sm p-12 text-center">
                        <div className="text-gray-400 mb-4">
                            <Search className="w-12 h-12 mx-auto" />
                        </div>
                        <h3 className="text-lg font-medium text-gray-900 mb-2">
                            Không tìm thấy chức năng
                        </h3>
                        <p className="text-gray-600">
                            {searchQuery
                                ? `Không có kết quả cho "${searchQuery}"`
                                : "Không có chức năng nào trong nhóm được chọn"}
                        </p>
                    </div>
                )}
            </div>

            {/* Loading overlay */}
            {isNavigating && (
                <div className="fixed inset-0 bg-black bg-opacity-50 flex items-center justify-center z-50">
                    <div className="bg-white rounded-lg p-6 flex items-center space-x-3">
                        <div className="animate-spin rounded-full h-6 w-6 border-b-2 border-blue-600"></div>
                        <span>Đang chuyển trang...</span>
                    </div>
                </div>
            )}
        </div>
    );
};

export default Dashboard;
